    findimg = () => {
        if (this.state.weaponname !== "none") {
            return imgs[this.state.weaponname]
        } else {
            // log("返回空武器图")
            return imgs.noweapon
        }
    }
    setweaponname = (event) => {
        if (event.target.value === "none") {
            alert("不能选择空武器")
        } else {
            this.setState({weaponname: event.target.value}, this.setweaponsecprop)
            // log("weaponname = ", this.state.weaponname)
            this.setState({weapontips: weapondb[this.props.weapon][event.target.value]["tips"]})
            // log("tips == ", weapondb[this.props.weapon][event.target.value]["tips"])
            this.update()
        }

    }
    setweaponlv = (event) => {
        if (event.target.value === "none") {
            alert("不能选择空武器等级")
        } else {
            this.setState({weaponlv: event.target.value}, this.setweaponvalue(event), this.setState({weaponeffect: "none"}))
            this.update()
        }

    }
    setweaponrank = (event) => {
        this.setState({weaponrank: event.target.value},this.setState({weaponeffect: "none"}, log("回调改weaponeffect--none=", this.state.weaponeffect)))

        this.update()
    }
    setweaponsecprop = () => {
        this.setState({weapon_secprop: weapondb[this.props.weapon][this.state.weaponname]["weapon_secprop"]})
        let a = weapondb[this.props.weapon][this.state.weaponname]["weapon_secprop"]
        let m = {
            "atk_buff": "攻击力%",
            "def_buff": "防御力%",
            "hp_buff": "生命值%",
            "elemenal_mastery": "元素精通",
            "crit_rate": "暴击率",
            "crit_dmg": "暴击伤害",
            "energy_recharge": "元素充能",
            "physical_dmg_bonus": "物理伤害%"
        }
        this.setState({weapon_secpropchr: m[a]}, this.update)



    }
    setweaponvalue = (event) => {
        let a = weapondb[this.props.weapon][this.state.weaponname][event.target.value]
        let key = Object.keys(a)
        let value = Object.values(a)
        key.map(v => {
            let name = "weapon_" + v
            // let value =
            this.setState({[name]: a[v]})
        })
        this.setState({weapon_secpropvalue: value[1]}, this.update)

        // log("kvkvkv", key, value, value[1])

    }
    setweaponeffect = () => {
        // log("ppppppppppp", this.state.weaponeffect, weapondb[this.props.weapon][this.state.weaponname]["effect"][0][this.state.weaponeffect])
        let a = weapondb[this.props.weapon][this.state.weaponname]["effect"][this.state.weaponeffectnum - 1][this.state.weaponeffect]
        // log("aaaaa", this.state.weaponeffectnum - 1)
        let str = ""
        let val = {}
        let arr1 = Object.keys(a)
        let arr2 = Object.values(a)
        for (let i = 0; i < arr1.length; i++) {
            str = str + chr[Object.keys(a)[i].slice(7, )] + " : " + Object.values(a)[i][this.state.weaponrank - 1] + "\n"
        }
        for (let j = 0; j< arr1.length; j++) {
            val[Object.keys(a)[j].slice(7, )] = Object.values(a)[j][this.state.weaponrank - 1]
        }
        this.setState({weaponeffectstring: str})
        this.setState({weaponeffectvalue: val}, this.update)
        // log("a = ", chr[Object.keys(a)[0].slice(7, )], Object.values(a)[0][this.state.weaponrank], this.state.weaponrank)
        // log("str = ", str)
        // log("value = ", val)


    }
    onEffectupdate= (effect, value) => {
        this.setState({weaponeffect: effect}, this.setweaponeffect)
        this.setState({weaponeffectnum: value}, this.update)

    }
    additioneffectupdate = () => {
        let a = document["additioneffect"]
        let additionalbufflist = this.state.additionalbufflist
        let sel = a["additioneffectsel"].value
        let val = a["additioneffectval"].value
        if (persentval.includes(sel)) {
            val = val / 100
        }
        additionalbufflist[sel] = val
        this.setState({additionalbufflist: additionalbufflist}, this.update)
        this.props.flash0()
        // log("iiiii",additionalbufflist, this.state.weaponeffectstring, this.state.weaponeffectvalue)
    }
    additioneffectdelate = (value) => {
        let additionalbufflist = this.state.additionalbufflist
        delete additionalbufflist[value]
        this.setState({additionalbufflist: additionalbufflist}, this.update)
        this.props.flash0()
    }
    twopyro = (event) => {
        let a = event.target.checked
        let v = this.state.weapon_atk_buff
        if (a) {
            this.setState({weapon_atk_buff: (v + 0.25)}, this.update)
        } else {
            this.setState({weapon_atk_buff: (v - 0.25)}, this.update)
        }

    }
