import React, { Component } from 'react'

import weapondb from "./db/weapon.json"

import * as imgs from "./module.ts"

const log = console.log.bind(console)


class Effect extends Component{
    constructor(props) {
        super(props)
        this.state = {
            name: this.props.effect0,
            type: this.props.type0,
            weaponeffectname: [],
            weaponeffectlist: [],
            effect: "",
        }

    }
    geteffect = () => {
        if (this.state.effect === "none") {
            alert("重新选择")
        } else {
            this.props.onEffectupdate(this.state.effect, this.state.effect.slice(-1,))
            // log("改变", this.state.effect.slice(-1,))
        }

    }
    oneffectchange = (event) => {
        this.setState({effect: event.target.value}, this.geteffect)


    }
    componentWillReceiveProps = () => {
        let list = []
        // log("effect插件", this.props.effect0, this.props.type0)
        if (this.props.type0 === "none") {
            this.setState({weaponeffectname: []})
            this.setState({weaponeffectlist: []})
            this.setState({effect: ""})
        } else if (this.props.effect0 !== "none") {
            // log("mmmmmmmm", Object.keys(weapondb[this.props.type0][this.props.effect0]["effect"][1])[0])
            for (let i = 0; i < weapondb[this.props.type0][this.props.effect0]["effect"].length; i++) {
                list.push(Object.keys(weapondb[this.props.type0][this.props.effect0]["effect"][i])[0])
            }
            // log("list  === ", list)
            this.setState({weaponeffectname: list})
        }
        // log("aaaaaaname", this.state.weaponeffectname)

    }
    componentWillUpdate= () => {


    }
    componentDidUpdate= () => {

        // log("112233", this.state.effect)


    }
    componentWillMount = () => {


    }
    componentDidMount = () => {

    }
    render() {
        // log("effect", this.state.weaponeffectlist)
        return(
            <select name="" id="" onChange={this.oneffectchange} value={this.props.effect}>
                <option value="none">none</option>
                {this.state.weaponeffectname.map(a => (<option value={a}>{a}</option>))}
            </select>

        )
    }

}

export default Effect
