    // 计算抗性系数k
    analyzeK = (name) => {
        let k = 0
        name = name/100
        switch (true) {
            case name<0:
                k = 1 - (name / 2)
                break
            case name>=0 && name<0.75:
                k = 1 - name
                break
            case name>0.75:
                k = 1 / (1+4*name)
                break
        }
        return k
    }
    // 计算防御系数
    enemynumK= (lv, enemylv, enemydef) => {
        let k = (100+parseFloat(lv.slice(2,4)))/(100+parseFloat(lv.slice(2,4))+((100+parseFloat(enemylv))*(1+parseFloat(enemydef)/100)))
        return k
    }
