    res0 = (name ,value, porn, event) => {
        let a = document["enemy"]
        let e = event.target.checked
        let pORn = porn
        // log(e)
        if (e === false) {
            pORn = !pORn
        }
        if (name === "all") {
            let enemyantpyro = parseFloat(this.state.enemyantpyro)
            let enemyanthydro = parseFloat(this.state.enemyanthydro)
            let enemyantelectro = parseFloat(this.state.enemyantelectro)
            let enemyantgeo = parseFloat(this.state.enemyantgeo)
            let enemyantdendro = parseFloat(this.state.enemyantdendro)
            let enemyantanemo = parseFloat(this.state.enemyantanemo)
            let enemyantcyro = parseFloat(this.state.enemyantcyro)
            let enemyantphysical = parseFloat(this.state.enemyantphysical)
            let enemyantdefance = parseFloat(this.state.enemyantdefance)
            switch (true) {
                case pORn === true:
                    enemyantpyro = enemyantpyro + value
                    enemyanthydro = enemyanthydro + value
                    enemyantelectro = enemyantelectro + value
                    enemyantgeo = enemyantgeo + value
                    enemyantdendro = enemyantdendro + value
                    enemyantanemo = enemyantanemo + value
                    enemyantcyro = enemyantcyro + value
                    enemyantphysical = enemyantphysical + value
                    enemyantdefance = enemyantdefance + value
                    break;
                case pORn === false:
                    enemyantpyro = enemyantpyro - value
                    enemyanthydro = enemyanthydro - value
                    enemyantelectro = enemyantelectro - value
                    enemyantgeo = enemyantgeo - value
                    enemyantdendro = enemyantdendro - value
                    enemyantanemo = enemyantanemo - value
                    enemyantcyro = enemyantcyro - value
                    enemyantphysical = enemyantphysical - value
                    enemyantdefance = enemyantdefance - value
                    break
            }

            this.setState({enemyantpyro: enemyantpyro})
            a["enemyantpyro"].value = enemyantpyro
            this.setState({enemyanthydro: enemyanthydro})
            a["enemyanthydro"].value = enemyanthydro
            this.setState({enemyantelectro: enemyantelectro})
            a["enemyantelectro"].value = enemyantelectro
            this.setState({enemyantgeo: enemyantgeo})
            a["enemyantgeo"].value = enemyantgeo
            this.setState({enemyantdendro: enemyantdendro})
            a["enemyantdendro"].value = enemyantdendro
            this.setState({enemyantanemo: enemyantanemo})
            a["enemyantanemo"].value = enemyantanemo
            this.setState({enemyantcyro: enemyantcyro})
            a["enemyantcyro"].value = enemyantcyro
            this.setState({enemyantphysical: enemyantphysical})
            a["enemyantphysical"].value = enemyantphysical
            this.setState({enemyantdefance: enemyantdefance})
            a["enemyantdefance"].value = enemyantdefance

        } else {
            let n = parseFloat(this.state[name])
            switch (true) {
                case pORn === true:
                    n = n + value
                    break;
                case pORn === false:
                    n = n - value
                    break
            }
            this.setState({[name]: n})
            a[name].value = n
        }
        this.handeldef()

    }
