import React, { Component } from 'react'
import beepmp3 from './img/beep.mp3';
import zero from './img/zero.mp3'
import "./beep.css"

const log = console.log.bind(console)

var timeid0 = null
var timeid1 = null
class Beep extends Component {
    constructor(props) {
        super(props)
        this.state = {
        }
}
    playaudio = () => {
        // log("aaaaaaaaaaaaaaa")
        this.beepplay.play()
    }

    holdtime = () => {
        let d = new Date()
        let n = d.getTime()
        let a = this.beepplay
        timeid1 = setInterval(function() {
            let s = new Date() - n
            if (s > 12000) {
                n = new Date() - 0
                a.play()

            }
        }, 50)
    }
    beepplayloop = () => {
        timeid0 =  setInterval(this.playaudio, 12000)
        // log("开始")
    }
    clickstart = () => {
        this.clilcend()
        this.zero.play()
        setTimeout(
            this.holdtime, 2500)
        log("start")
    }
    clilcend = () => {
        this.zero.pause()
        this.beepplay.pause()
        window.clearInterval(timeid0)
        window.clearInterval(timeid1)
        log("stop")
    }

    render() {

        return(
            <div className="beep">
                <div id="beep01">
                    <div id="beep01text">(旧深渊)冰爪提醒：点击开始，倒数3秒后在在深渊有冰爪的关卡副本（深渊12）中同时开始，会鸣笛提醒冰爪即将出现（不完全准确）
                    </div>
                    <audio src={zero} ref={(audio) => {this.zero = audio}}/>
                    <audio src={beepmp3} ref={(audio) => {this.beepplay = audio}}/>
                    <button onClick={this.clickstart}>开始</button>
                    <button onClick={this.clilcend}>关闭</button>
                </div>
            </div>


        )


    }

}
export default Beep
