        delete newstr.hero
        delete newstr.hero_weapon
        delete newstr.hero_attr
        delete newstr.lv
        delete newstr.evolv
        delete newstr.tips
        // log("newstr = ", newstr)
        let array = Object.keys(newstr)
        // log("keys", array[0])
        // this.setState({hp: herodb[a][b]["hp"]})

