    playsound = () => {
        let audio1 = this.gachaaudio01
        this.gachaaudio0.play()
        setTimeout(function() {
            audio1.play()
        }, 5000)
    }
    reward = (array, color, count) => {

        document.querySelector("#showresult").innerHTML = ""
        let c = 0
        let n = this.state.allcount + count
        // let s = this.state.skipmark
        this.setState({allcount: n})
        let mark = this.state.skipmark
        let result = this.showresult
        let play0 = this.playsound
        if (mark === false) {
            play0()
        }
        let a0 = setInterval(function() {
            if (c > 3 && color === "yellow" && mark !== true) {
                document["gachaform"]["effectbox"].style.color = "yellow"
            }
            if (c > 3 && color === "purple" && mark !== true) {
                document["gachaform"]["effectbox"].style.color = "#8A2BE2"
            }
            document["gachaform"]["effectbox"].value = stringarray[c]
            c++;
            if (c > 10) {
                result(array)
                clearInterval(a0)
            }
            if (mark === true) {
                result(array)
                clearInterval(a0)
            }
        }, 500)

        document["gachaform"]["effectbox"].value = stringarray[10]
        document["gachaform"]["effectbox"].style.color = "aqua"

    }
    showresult = (array) => {
        document["gachaform"]["effectbox"].value = ""
        let a = array
        let gold0 = this.state.residenthero
        let gold1 = this.state.uphero
        let gold2 = this.state.residentweapons
        let gold3 = this.state.upweapon
        let purple0 = this.state.upfourstarhero
        let pulple1 = this.state.fourstarhero
        let pulple2 = this.state.fourstarweapon
        let gold = [...gold0, ...gold1, ...gold2, ...gold3]
        let newgold = new Set(gold)
        newgold = [...newgold]
        let puple = [...pulple1, ...pulple2, ...purple0]
        let newpuple = new Set(puple)
        newpuple = [...newpuple]
        log("a = ", a)
        a.map(v => {
            if (newgold.includes(v)) {
                let m = document.getElementById("showresult")
                // log("m = " ,m)
                let n = document.createElement("span")
                n.className = "gold"
                n.innerHTML = v + "&nbsp;"
                // let n = `<span id="gold">${v}</span>`
                m.appendChild(n)
            } else if (newpuple.includes(v)) {
                let m = document.getElementById("showresult")
                // log("m = " ,m)
                let n = document.createElement("span")
                n.className = "puple"
                n.innerHTML = v + "&nbsp;"
                // let n = `<span id="gold">${v}</span>`
                m.appendChild(n)

            } else {
                let m = document.querySelector("#showresult")
                let n = document.createElement("span")
                n.className = "blue"
                n.innerHTML = v + "&nbsp;"
                // let n = `<span id="gold">${v}</span>`
                m.appendChild(n)
            }
        })
        log("fdfdfdfdf")
    }
    skip = (e) => {
        let a = this.state.skipmark
        if (a === false) {
            this.setState({skipmark: !a})
            // log(this.state.skipmark, e)
            document["gachaform"]["skipbutton"].value = "恢复"
            // log("000",document["gachaform"]["skipbutton"].value)
        } else {
            this.setState({skipmark: !a})
            // log(this.state.skipmark, e)
            e.target.value = "跳过动画"
        }
        // document["gachaform"]["effectbox"].style.color = "yellow"
    }
    componentDidMount = () => {
        let uphero = []
        let a = document["gachaform"]
        a = a["uphero"].value
        uphero.push(a)
        this.setState({uphero: uphero})
        let upweapon = []
        let b = document["gachaform"]["upweapon1"].value
        let c = document["gachaform"]["upweapon2"].value
        upweapon.push(b,c)
        this.setState({upweapon: upweapon})
        let upfourstarhero = []
        let d = document["gachaform"]["uphero1"].value
        let e = document["gachaform"]["uphero2"].value
        let f = document["gachaform"]["uphero3"].value
        upfourstarhero.push(d,e,f)
        this.setState({upfourstarhero: upfourstarhero})
    }
